.AppContainer {
  width: 100%;
  padding: 20px;
  position: relative;
}
.AppContainer::after {
  content: "";
  background-image: url("../src/img/bg-image.jpg");
  background-size: contain;
  opacity: 0.2;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  z-index: -1;
}
.FormContainer {
  width: 60%;
  background-color: azure;
  margin-left: auto;
  margin-right: auto;
  border-radius: 10px;
  padding: 20px;
}

@media screen and (max-width: 600px) {
  body {
    font-size: 0.8rem;
  }
  h2 {
    font-size: 1.2rem;
  }
  .AppContainer {
    padding: 10px;
  }
  .FormContainer {
    width: 100%;
    background-color: azure;
    margin-left: auto;
    margin-right: auto;
    border-radius: 10px;
    padding: 10px;
  }
}
